@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
}

#navbar__appbar__bgcolor {
  background: #f2f5fa;
}
#navbar__top {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
#navbar__top > img {
  width: 170px;
}
.navbar__socialicon {
  width: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 80px;
  margin-left: 2.5rem;
}
.list__drawer {
  padding-top: 90px !important;
}
.list__drawer > div {
  margin: 20px 0px;
  text-align: center;
}
@media (min-width: 750px) {
  .navbar__socialicon {
    width: 200px;
  }
  #navbar__top > img {
    width: 190px;
  }
}

.home__main {
  background: #f2f5fa;
  padding-top: 2rem;
  padding-left: 4rem;
  font-family: "Montserrat", sans-serif;
}
.home__main h1 {
  font-size: 3rem;
  font-weight: 400;
}
#forbold {
  font-weight: 900;
  color: #6a5fde;
}
.home__content__paper {
  width: 90%;
  padding: 1rem;
  color: #2c2e36;
}
.home__content__image {
  text-align: center;
}
.home__content__image img {
  margin-top: 2rem;
  width: 300px;
}

@media (min-width: 750px) {
  .home__main {
    padding-top: 3rem;
    padding-left: 6rem;
  }
  .home__content__paper {
    width: 80%;
    padding: 2rem;
  }
  .home__content__image img {
    margin-top: 7rem;
    width: 320px;
  }
}


